import React from 'react'
import { Form } from 'react-bootstrap'
import { SORT_OPTIONS } from './constants'

function Sort({ sortedList, setSortedList }) {




    // {
    //     "id": 1,
    //     "title": "Fjallraven - Foldsack No. 1 Backpack, Fits 15 Laptops",
    //     "price": 109.95,
    //     "description": "Your perfect pack for everyday use and walks in the forest. Stash your laptop (up to 15 inches) in the padded sleeve, your everyday",
    //     "category": "men's clothing",
    //     "image": "https://fakestoreapi.com/img/81fPKd-2AYL._AC_SL1500_.jpg",
    //     "rating": {
    //         "rate": 3.9,
    //         "count": 120
    //     }
    // }

    // {
    //     "id": 2,
    //     "title": "Mens Casual Premium Slim Fit T-Shirts ",
    //     "price": 22.3,
    //     "description": "Slim-fitting style, contrast raglan long sleeve, three-button henley placket, light weight & soft fabric for breathable and comfortable wearing. And Solid stitched shirts with round neck made for durability and a great fit for casual fashion wear and diehard baseball fans. The Henley style round neckline includes a three-button placket.",
    //     "category": "men's clothing",
    //     "image": "https://fakestoreapi.com/img/71-3HjGNDUL._AC_SY879._SX._UX._SY._UY_.jpg",
    //     "rating": {
    //         "rate": 4.1,
    //         "count": 259
    //     }
    // }

    const handleSort = (e) => {
        const selectedSortID = e.target.value;
        console.log('selectedSortID', selectedSortID, +selectedSortID);
        const { sortKey, increasing } = SORT_OPTIONS.find(({ id }) => id === selectedSortID);

        const list = [...sortedList];
        if (sortKey === "rating") {
            if (increasing) {
                list.sort((product_1, product_2) => product_1[sortKey].rate - product_2[sortKey].rate)
            }
            else {
                list.sort((product_1, product_2) => product_2[sortKey].rate - product_1[sortKey].rate)
            }
        }
        if (increasing) {
            list.sort((product_1, product_2) => product_1[sortKey] - product_2[sortKey])
        }
        else {
            list.sort((product_1, product_2) => product_2[sortKey] - product_1[sortKey])
        }

        setSortedList(list)
    }

    return (
        <Form.Select size="sm" onChange={handleSort}>
            <option selected disabled>Select sort option</option>
            {SORT_OPTIONS.map(({ id, label }) => <option value={id} key={id}>{label}</option>)}
        </Form.Select>
    )
}

export default Sort